import * as React from 'react';
import { NAMES, STYLES } from './App';
import { ProgressBar } from 'react-bootstrap';

export interface ProgressProps {
    which: number;
    level: number;
    cost: number;
    currentCount: number;
    totalCount: number;
    countProgress: number;
}

export default class ProgressPlus extends React.Component<ProgressProps, {}> {

  render() {
    let which = this.props.which;
    let smth = `(${this.props.level})`;
    // let speedLabel = `Hire master ${NAMES[which+4]} (${engine.state.speeds[which]}) Speeds up all ${NAMES[which+4]}s \$${this.costSpeed()}`
    // let btn = (<Button onClick={this.levelUpSpeed}>{speedLabel}</Button>)
    
    return (
      <div>
          <div>{NAMES[which]}: {this.props.currentCount}</div>
          <div>{NAMES[this.props.which + 4]}s: {smth}</div>
          <ProgressBar bsStyle={STYLES[which]} now={this.props.countProgress} />
          <div>Base {NAMES[which]} /s: {(this.props.level ** 1.4).toFixed(2)}</div>
      </div>
    );
  }
}



