interface EngineState {
  totalCounts: number[];
  currentCounts: number[];
  levels: number[];
  speeds: number[];
  upgradeResets: number[];
  happiness: number;
  gold: number;
  population: number;
  totalPop: number;
  upgrades: number[][];
  countProgress: number[];
  achs: boolean[];
  buildings: Building[];
  goldDecay: number;
}

export class Building {

  public count: number = 0;

  constructor(public name: string, private base: number, private growth: number, public production: number){

  }

  public cost = ( ) => {
    let val = ((this.base ) * (1 + (this.growth * (this.count))));
    val = Math.floor(val);
    return val;
  }

}

