import * as React from 'react';
import Engine from './Engine';
import {Button, Glyphicon, OverlayTrigger, Tooltip} from 'react-bootstrap';

interface achievment {
  name: string;
  tooltip: string;
}

export const achievements: achievment[] = [
  {name:"Empire",tooltip:'Get 100 Imperialism'},
  {name:"Religion",tooltip:'Get 100 Religion'},
  {name:"Corporation",tooltip:'Get 100 Commerce'},
  {name:'College',tooltip:'Get 100 Knowledge'},
  {name:'Village',tooltip:'Get 100 Population'},
  {name:'Hamlet',tooltip:'Get 10000 Population'},
  {name:'Settlement',tooltip:'Get 100000 Population'},
  {name:'Town',tooltip:'Get 1000000 Population'},

  {name:'Quality Outposts',tooltip:'Get 5 Outposts'},
  {name:'Reknowned Outposts', tooltip:'Get 10 Outposts'},
  {name:'Quality Keeps',tooltip:'Get 10 Keeps'},
  {name:'Reknowned Keeps',tooltip:'Get 20 Keeps'},
  {name:'Quality Castles',tooltip:'Get 10 Castles'},
  {name:'Reknowned Castles',tooltip:'Get 100 Castles'},

  {name:'Quality Temples',tooltip:'Get 5 '},
  {name:'Reknowned Temples', tooltip:'Get 10 '},
  {name:'Quality Churches',tooltip:'Get 10 '},
  {name:'Reknowned Churches',tooltip:'Get 20 '},
  {name:'Quality Holy Grounds',tooltip:'Get 10 '},
  {name:'Reknowned Holy Grounds',tooltip:'Get 100 '},

  {name:'Quality Trade Routes',tooltip:'Get 5 '},
  {name:'Reknowned Trade Routes', tooltip:'Get 10 '},
  {name:'Quality Markets',tooltip:'Get 10 '},
  {name:'Reknowned Markets',tooltip:'Get 20 '},
  {name:'Quality Banks',tooltip:'Get 10 '},
  {name:'Reknowned Banks',tooltip:'Get 100 '},

  {name:'Quality Schools',tooltip:'Get 5 '},
  {name:'Reknowned Schools', tooltip:'Get 10 '},
  {name:'Quality Universities',tooltip:'Get 10 '},
  {name:'Reknowned Universities',tooltip:'Get 20 '},
  {name:'Quality Institutes',tooltip:'Get 10 '},
  {name:'Reknowned Institues',tooltip:'Get 100 '}

]

export interface ProgressProps {
    engine: Engine;
}

export default class Toggles extends React.Component<ProgressProps, {}> {

  constructor(props: ProgressProps) {
    super(props);

    this.state = {
      completed: 0,
    };
  }

  attract=() => {
 
    this.props.engine.attract();
  }

  do=(which: number)=> {
 
    this.props.engine.levelUp(which);
  }

  render() {
    let acharray = this.props.engine.state.achs;    
    let achs = achievements.map(function(ach, index){
      let tooltip = (<Tooltip>{ach.tooltip}</Tooltip>)
      let glyph = acharray[index]?  'ok' : 'remove';
      return (<OverlayTrigger overlay={tooltip}><Button bsSize={'xs'}><Glyphicon glyph={glyph}/>{ach.name}</Button></OverlayTrigger>)
    })

   return (
      <div>
      Achievements
      <div style={{overflowY :'scroll',height :200, display:'flex',flexWrap:'wrap'}}>
        {achs}
      </div>
      </div>
    );
  }
}



