import * as React from 'react';
import Engine from './Engine';
import {Button, OverlayTrigger, Tooltip} from 'react-bootstrap';

export const UPGRADE_NAMES: string[][] = [
  ['Villager Attraction' ,'Villager Expansion' ,'Pillage' ,'Conquer'] ,//villager +,
  ['Minimum Happiness' ,'Maximum Happiness' ,'Tithing' ,'Missionaries'] ,//min happiness+, max happiness+
  ['Taxation' ,'Investing' ,'Interest' ,'Welfare'] ,//
  ['Trait Identification' ,'Teacher Training' ,'Sell Knowledge' ,'Building Efficience']//vill cost -, vill cost for teacher -
];

export interface ProgressProps {
    engine: Engine;
    which: number;
}

export default class Upgrade extends React.Component<ProgressProps, {}> {

  attract=( )=>{ 
    this.props.engine.state.population += 1;
    this.props.engine.state.totalPop += 1;
    this.props.engine.notify();
  }

  do(which2: number){
    this.props.engine.upgrade(this.props.which, which2);
  }

  tooltips: string[] = [
    'Increases Villagers Attracted by 1',
    'Increases villager per second by 1 * factor of Imperialsm',
    'Allows Villagers to pillage gold',
    'Allows Villagers and leaders to conquer more population',
    
    'Increases minimum happiness by 15%',
    'Increases maximum happiness by 10%',
    'Increases gold per second by a factor of faith and population',
    'Allows Preachers to gather population',

    'Increases gold per second by a factor of commerce and specialists',
    'Allows investing of gold',
    'Gives .001% interset per second per level',
    'Decresaes happiness degen',

    'Decreases the cost of finding specialists',
    'Decreases the cost of training teachers',
    'Allows selling knowledge for gold',
    'Decreases the cost of all buildings'
  ]

    render() {
    
    let fn = [
      this.do.bind(this, [0]),
      this.do.bind(this, [1]),
      this.do.bind(this, [2]),
      this.do.bind(this, [3]),
    ]
    let engine = this.props.engine
    let which = this.props.which;
    let tooltips = this.tooltips;
    //let state = this.props.engine.state;
    let upgs = UPGRADE_NAMES[this.props.which].slice( 0,4)
    let btns = upgs.map(function(upg, index){
      let tooltip = <Tooltip>{tooltips[index + (which * 4)]}</Tooltip>
      return <OverlayTrigger overlay={tooltip}><Button onClick={fn[index]} key={index}>{UPGRADE_NAMES[which][index]}-{engine.state.upgrades[which][index]}</Button></OverlayTrigger>
    })
    //console.log(btns);
    
    //let label = `Attract Villager (${state.population}/${state.totalPop})`;
    return (
      <div>
        {`Upgrade Cost: ${this.props.engine.upgradeCost(which)}`}<br/>
        <div style={{display:"flex",flexDirection:'column'}}>
          {btns}
        </div>
      </div>
    );
  }
}



