import * as React from 'react';
import './App.css';
import ProgressPlus from './ProgressPlus';
import Happiness from './Happiness';
import Population from './Population';
import Upgrade from './Upgrade';
import Engine from './Engine';
import Toggles from './Toggles';
import Buildings from './Buildings';
import Commands from './Commands';
import { ProgressBar } from 'react-bootstrap';
import { Grid, Row, Col } from 'react-bootstrap';

const logo = require('./logo.svg');
const logo2 = require('./logo2.svg');
declare let window: any;
  
export const NAMES: string[] = [
  'Imperialism', 'Faith', 'Commerce', 'Knowledge',
  'Leader', 'Preacher', 'Trader', 'Teacher'
];

export const STYLES: string[] = [
  'danger', 'warning', 'success', 'info'  
];

interface appState {
  game: boolean;
  engine: Engine;
}

class App extends React.Component<{}, appState> {
  constructor(props: undefined) {
    super(props);
    let engine = new Engine(this.notify);
    this.state = {
      game: false, engine
    };
  }

  notify= () => { 
     this.setState({engine: this.state.engine});
  }

  gameCalled= () => {

    window.kongregate = window.kongregateAPI.getAPI();
    let kg = window.kongregate;
    console.log(kg.services);
    this.setState({game: true});
  }

  componentDidMount() {
    window.kongregateAPI.loadAPI(this.gameCalled);
  }

  hardReset= () => {
    localStorage.clear();
    this.setState({engine: new Engine(this.notify)});
  }

  render() {
    let state = this.state.engine.state;
    return (
      <Grid className="App">
        <Row><Col>
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" onClick={this.state.engine.reset}/>
          <img src={logo2} className="App-logo" alt="logo" onClick={this.hardReset}/>
          <h2>Welcome to my speed run. </h2>  
        </div>
        </Col></Row>
        <Row style={{backgroundColor:'lightgrey'}}>
          <Col md={2}>
          Gold Gain: {(this.state.engine.state.goldDecay * 100).toFixed(3)}%
          </Col>
          <Col>
          <ProgressBar now={this.state.engine.state.goldDecay * 100} />
          </Col>
        </Row>
        <Row>
        <Col md={6} style={{backgroundColor:'lightgrey'}}>
          <Population engine={this.state.engine}/>
        </Col>
        <Col md={3} style={{backgroundColor:'lightgrey'}}>
          <Happiness engine={this.state.engine}/>
        </Col>
        <Col md={3} style={{backgroundColor:'lightgrey'}}>
          {`Gold: ${this.state.engine.state.gold}`}<br/>
          {`Prestige: ${this.state.engine.state.upgradeResets}`}<br/>
          {`Prestige (new): ${this.state.engine.getPrestige()[0]},${this.state.engine.getPrestige()[1]},${this.state.engine.getPrestige()[2]},${this.state.engine.getPrestige()[3]},${this.state.engine.getPrestige()[4]}`}
        </Col>
        </Row>
        <Row><Col md={3} style={{backgroundColor:'tomato'}}>
           {`${this.state.engine.powerPop} population /s`}        
          <ProgressPlus which={0} cost={this.state.engine.cost(0)} countProgress={state.countProgress[0]} currentCount={state.currentCounts[0]} level={state.levels[0]} totalCount={state.totalCounts[0]} />
          <Upgrade engine={this.state.engine} which={0} />
          <Buildings engine={this.state.engine} which={0} />
          </Col ><Col md={3} style={{backgroundColor:'gold'}}>
           {`${this.state.engine.faithGold} tithes /s`}
          <ProgressPlus which={1} cost={this.state.engine.cost(1)} countProgress={state.countProgress[1]} currentCount={state.currentCounts[1]} level={state.levels[1]} totalCount={state.totalCounts[1]} />
          <Upgrade engine={this.state.engine} which={1} />
          <Buildings engine={this.state.engine} which={1} />
          </Col><Col md={3} style={{backgroundColor:'lightgreen'}}>
           {`${this.state.engine.taxAmmount} taxes /s `} - {`${this.state.engine.interest()} interest /s`}        
          <ProgressPlus which={2} cost={this.state.engine.cost(2)} countProgress={state.countProgress[2]} currentCount={state.currentCounts[2]} level={state.levels[2]} totalCount={state.totalCounts[2]} />
          <Upgrade engine={this.state.engine} which={2} />
          <Buildings engine={this.state.engine} which={2} />
          </Col><Col md={3} style={{backgroundColor:'lightblue'}}>
           {`${this.state.engine.knowledgeTraits.toFixed(3)} other resources /s`}          
          <ProgressPlus which={3} cost={this.state.engine.cost(3)} countProgress={state.countProgress[3]} currentCount={state.currentCounts[3]} level={state.levels[3]} totalCount={state.totalCounts[3]} />
          <Upgrade engine={this.state.engine} which={3} />
          <Buildings engine={this.state.engine} which={3} />
        </Col></Row>
        <Row>
          <Col md={4} style={{backgroundColor:'brown'}}>
            <Toggles engine={this.state.engine}/>
          </Col> 
          <Col md={2} style={{backgroundColor:'black'}}>
            <Commands engine={this.state.engine}/>
          </Col> 
        </Row>
  
      </Grid>
    );
  }
}

export default App;



