import * as React from 'react';
import Engine from './Engine';
import {Button} from 'react-bootstrap';
import {NAMES, STYLES} from './App';

export interface ProgressProps {
    engine: Engine;
}

interface PState {
  completed: number;
   buyX: number;
}

export default class Populaion extends React.Component<ProgressProps, PState> {

  constructor(props: ProgressProps) {
    super(props);

    this.state = {
      completed: 0,
      buyX: 1
    };
  }

  attract= () => {
    this.props.engine.attract();
  }

  do= (which: number) => {
    for (let x = 0; x < this.state.buyX; x++) {
      this.props.engine.levelUp(which);
    }
  }

  buymore= () => {
    let cur = this.state.buyX;
    if (cur === 1) {
      this.setState({buyX: 10})
    } else if (cur === 10) {
      this.setState({buyX :100})
    } else if (cur === 100) {
      this.setState({buyX:1})
    }
  }

  render() {
    let state = this.props.engine.state;
    let label = `Attract Villager`;
    
    let fn = [
      this.do.bind(this, [0]),
      this.do.bind(this, [1]),
      this.do.bind(this, [2]),
      this.do.bind(this, [3]),
    ]

    let buyXLabel = `Buy ${this.state.buyX}x`

    let cost1 = this.props.engine.cost(1)
    let cost2 = this.props.engine.cost(3)
    let levelLabel = `Find a specialist amongst ${cost1} (${cost2}) villagers`;
    let btns = NAMES.slice(4,8).map(function(name, index){
      return <Button onClick={fn[index]} bsStyle={STYLES[index]} key={index}>{name}</Button>
    })
    return (
      <div>
        <Button onClick={this.attract} bsSize={'large'}>{label}</Button>{`Population: ${state.population}`}<br/>
        {levelLabel}<br/>
        <Button onClick={this.buymore}>{buyXLabel}</Button>{btns}
      </div>
    );
  }
}



